.swiper-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 4px;
}

.swiper-buttons__button {
  position: relative;
  width: 37px;
  height: 35px;
  background-color: #0266c1;
  border: 1px solid transparent;
  cursor: pointer;
}
.swiper-buttons__button::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 13px;
  height: 1px;
  background-color: #ffffff;
}
.swiper-buttons__button:hover {
  background-color: #ffffff;
  border: 1px solid #1c3374;
}
.swiper-buttons__button:hover::before {
  background-color: #1c3374;
}
.swiper-buttons__button:hover .swiper-buttons__arrow {
  border-top: 1px solid #1c3374;
  border-right: 1px solid #1c3374;
}
.swiper-buttons__button:focus-visible {
  background-color: #ffffff;
  border: 1px solid #1c3374;
  outline: none;
}
.swiper-buttons__button:focus-visible::before {
  background-color: #1c3374;
}
.swiper-buttons__button:focus-visible .swiper-buttons__arrow {
  border-top: 1px solid #1c3374;
  border-right: 1px solid #1c3374;
}
.swiper-buttons__button:focus {
  outline: none;
}
.swiper-buttons__button:active {
  background-color: #1c3374;
}
.swiper-buttons__button:active::before {
  background-color: #ffffff;
}
.swiper-buttons__button:active .swiper-buttons__arrow {
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
}
.swiper-buttons__button:disabled {
  background-color: rgba(2, 102, 193, 0.1);
  border: 1px solid transparent;
  cursor: not-allowed;
}
.swiper-buttons__button:disabled::before {
  background-color: #ffffff;
}
.swiper-buttons__button:disabled .swiper-buttons__arrow {
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  cursor: not-allowed;
}

.swiper-buttons__arrow {
  position: absolute;
  top: 14.5px;
  display: inline-flex;
  width: 5px;
  height: 5px;
  cursor: pointer;
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
}
.swiper-buttons__arrow--prev {
  left: 11px;
  transform: rotate(-135deg);
}
.swiper-buttons__arrow--next {
  right: 11px;
  transform: rotate(45deg);
}

