.advantages-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  gap: 30px 0;
  padding: 40px 20px;
  border: 1px solid $color-bright-blue-opacity10;

  @include vp-1199 {
    flex-basis: calc(50% - 15px);
    gap: 24px 0;
    padding: 30px 20px;
  }

  @include vp-767 {
    flex-basis: 100%;
    padding: 20px 10px;
  }
}

.advantages-card__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 54px;
  height: 54px;
  border: 1px solid $color-bright-blue;

  @include vp-1199 {
    width: 48px;
    height: 48px;
  }

  @include vp-767 {
    width: 42px;
    height: 42px;
  }
}

.advantages-card__text {
  display: flex;
  flex-direction: column;
  gap: 15px 0;
  text-align: center;
}

.advantages-card__text h3 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;

  @include vp-1199 {
    font-size: 16px;
    line-height: 16px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 14px;
  }
}


.advantages-card__text p {
  margin: 0;
  font-size: 14px;
  line-height: 22px;

  @include vp-1199 {
    font-size: 12px;
    line-height: 18px;
  }
}
